.profile { 
    padding: 2em 6em 3em 6em;
    height: 91vh;
    overflow-y: auto;
}

.profileTitle { 
    font-size: 2em;
    font-weight: 700;
    color: white;
    padding: 1em 0 ;

}

.profileInfoUp { 
    border: 0.1em solid white;
    width: 100%;
    height: 11em;
    border-radius: 1em 1em 0 0;
}

.profileInfoBottom { 
    position: relative;
    border-radius: 0 0 1em 1em;
    background-color: rgb(29, 32, 42);
    height: 7em;

    display: flex;
    align-items: center;

}
.profileInfoBottomAvatar { 
    user-select: none;
    cursor: pointer;
    position: absolute;
    top: -3em;
    left: 3em;
    width: 8em;
    height: 8em;
    border-radius: 100%;
    object-fit:cover;
}
.profileInfoBottomAvatar:hover { 
    filter: brightness(20%);
}

.profileInfoBottomDesc { 
margin-left: 13em;
display: flex;
flex-direction: column;
gap: 0.4em;
}
.profileInfoBottomDescName { 
    color: white;
    font-size: 1.4em;
    font-weight: 650;
    letter-spacing: 0.08em;

}
.profileInfoBottomDescRole { 
    color: rgb(134, 134, 134);
    font-size: 1.02em;
    font-weight: 400;
}
.profileInfoBottomBtn { 
    position: absolute;
    right: 2em;
    cursor: pointer;
    border: 0;
    border-radius: 0.5em;
    background-color: brown;
    color: white;
    font-size: 1.1em;
    font-weight: 400;
    padding: 0.5em 0.8em;
    transition: background-color .2s ease-in-out;
}
.profileInfoBottomBtn:hover { 
    background-color: rgb(120, 30, 30);
}



.profileContent { 
    margin-top: 2em;
    background-color: rgb(29, 32, 42);
    border-radius: 1em;
    padding: 2em;
}

.profileItems { 
    display: flex;
    align-items: center;
    gap: 1.7em;
    margin-bottom: 2em;
}
.profileItem{ 
    cursor: pointer;
    background-color: rgb(21, 21, 26);
    color: rgb(120, 120, 120);
    border-radius: 0.5em;
    padding: 0.6em 1em;
    font-size: 1em;
    font-weight: 600;
}
.profileItem:hover { 
    background-color: brown ;
    color: rgb(255, 255, 255);
}

.profileCarts { 
    display: flex;
    flex-wrap: wrap;
    gap: 2em;
}


@media only screen and (max-width: 768px) {
    .profile { 
        padding: 1em 0.5em 7em 0.5em;
        overflow-y: auto; /* Добавляем вертикальную прокрутку */
    }
    
    .profileTitle { 
        display: none;
    }
    
    .profileInfoUp { 
        border: 0.1em solid white;
        width: 100%;
        height: 7em;
        border-radius: 1em 1em 0 0;
    }
    
    .profileInfoBottom { 
        position: relative;
        border-radius: 0 0 1em 1em;
        background-color: rgb(29, 32, 42);
        height: 6em;
        display: flex;
        align-items: center;
    
    }
    .profileInfoBottomAvatar { 
        user-select: none;
        cursor: pointer;
        position: absolute;
        top: -2em;
        left: 3em;
        width: 6em;
        height: 6em;
        border-radius: 100%;
        object-fit:cover;
    }
    .profileInfoBottomAvatar:hover { 
        filter: brightness(20%);
    }
    
    .profileInfoBottomDesc { 
    margin-left: 11em;
    display: flex;
    flex-direction: column;
    gap: 0.2em;
    }
    .profileInfoBottomDescName { 
        color: white;
        font-size: 1.1em;
        font-weight: 600;
        letter-spacing: 0.05em;
    }
    .profileInfoBottomDescRole { 
        color: rgb(134, 134, 134);
        font-size: 0.9em;
        font-weight: 500;
    }
    .profileInfoBottomBtn { 
        position: absolute;
        right: 2em;
        cursor: pointer;
        border: 0;
        border-radius: 0.5em;
        background-color: brown;
        color: white;
        font-size: 0.9em;
        font-weight: 500;
        padding: 0.5em 0.8em;
        transition: background-color .2s ease-in-out;
    }
    .profileInfoBottomBtn:hover { 
        background-color: rgb(120, 30, 30);
    }

    
    
    .profileContent { 
        margin-top: 1em;
        background-color: rgb(29, 32, 42);
        border-radius: 1em;
        padding: 1em 0.5em;
    }
    
    .profileItems { 
        display: flex;
        align-items: center;
        gap: 1.7em;
        margin-bottom: 1em;
    }
    .profileItem{ 
        cursor: pointer;
        background-color: rgb(21, 21, 26);
        color: rgb(120, 120, 120);
        border-radius: 0.5em;
        padding: 0.6em 1em;
        font-size: 0.9em;
        font-weight: 600;
    }
    .profileItem:hover { 
        background-color: brown ;
        color: rgb(255, 255, 255);
    }
    
    .profileCarts { 
        display: flex;
        flex-flex-wrap: wrap;
        justify-content: space-between;
        gap: 0;
    }
}

@media only screen and (max-width: 580px) {
    .profileInfoBottomBtn { 
        display: none;
    }
    
    .profileInfoBottomDesc { 
        margin-left: 10em;
        display: flex;
        flex-direction: column;
        gap: 0.2em;
        }
        .profileInfoBottomDescName { 
            color: white;
            font-size: 1em;
            font-weight: 600;
            letter-spacing: 0.05em;
        }
        .profileInfoBottomDescRole { 
            color: rgb(134, 134, 134);
            font-size: 0.85em;
            font-weight: 600;
        }

        .profileInfoBottomAvatar { 
            user-select: none;
            cursor: pointer;
            position: absolute;
            top: -1em;
            left: 3em;
            width: 5em;
            height: 5em;
            border-radius: 100%;
            object-fit:cover;
        }
}