.search { 
    display: flex;
    align-items: center;
}
.searchInput{
    border: 0;
    background-color: rgb(55, 57, 70);
    padding: 0.7em 1.4em;
    border-radius: 0.8em 0 0 0.8em;
    width: 30em;
    color: white;
    font-size: 0.96em;
    font-weight: 400;
    letter-spacing: 0.08;
}
.searchInput::placeholder{
   color: white;
}

.searchBtn { 
    cursor: pointer;
    background-color: brown;
    padding: 0.7em 1.4em;
    border-radius: 0 0.8em 0.8em 0;
    font-size: 0.96em;
    color: white;
    font-weight: 500;
}
.searchBtn:hover { 
    background-color: rgb(106, 27, 27);
}