
.addItem { 
    height: 91vh;
    padding: 3em 6em;
    overflow-y: auto; /* Добавляем вертикальную прокрутку */
}
.container { 
    max-width: 900px;
    margin: 0 auto;
}

.addItemTitle { 
    color: white;
    font-size: 1.8em;
    font-weight: 750;
}

.addItemMenu { 
    display: flex;
    flex-direction: column;
    gap: 0.3em;
    margin-top: 2em;
}
.addItemMenuItem { 
    max-width: 22em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    padding: 0.75em 1em;
    border-radius: 0.6em;
    transition: background-color .3s ease-in-out;
}
.addItemMenuItem:hover { 
    background-color: var(--textBgHover);
}
.addItemMenuSvg { 
    width: 1.7em;
    stroke: white;
    stroke-width: 1.4;
}
.addItemMenuText { 
    font-size: 1em;
    font-weight: 500;
    color: white;
}
.addItemFormTitleText { 
    font-size: 1.8em;
    font-weight: 700;
    letter-spacing: 0.04em;
    color: white;
}
.addItemFormTitle { 
    display: flex;
    align-items: center;
    gap: 1em;
}
.addItemFormSvgP { 
    cursor: pointer;
    background-color: #466b91;
    padding: 0.5em;
    border-radius: 50%;
}
.addItemFormSvg { 
    width: 1.8em;
    stroke: white;
    stroke-width: 1.6;
}
.addItemFormDescText { 
    margin-top: 2em;
    color: white;
    font-size: 1em;
    font-weight: 600;
}
.addItemFormInputName { 
    width: 100%;
    border: 0;
    padding: 0.6em ;
    border-radius: 0.6em;
    color: black;
    font-size: 1em;
    font-weight: 700;
    margin-top: 0.5em;
}
.addItemFormInputName:hover { 
    background-color: #e7e7e7;
}

.addItemFormInputDescription { 
    border: 0;
    white-space: pre;
    width: 100%; /* Set the width to 100% */
    height: 7em;
  box-sizing: border-box;
  overflow-wrap: break-word;
  color: black;
  font-size: 1em;
  font-weight: 600;
  border-radius: 0.5em;
  padding: 0.5em;
}

.addItemFormInputDifficulty, .addItemFormInputLearningType { 
    color: black;
    font-size: 1.2em;
    border-radius: 0.5em;
    padding: 0.2em 0.6em;
    font-weight: 600;
    cursor: pointer;
}
.addItemFormInputDifficulty option { 
    color: black;
}
.addItemFormInputLearningType option { 
    color: black;
}

.addItemFormInputPhone, .addItemFormInputPrice, .addItemFormInputDuration { 
    width: 100%;
    border: 0;
    border-radius: 0.5em;
    padding: 0.4em 1em;
    color: black;
    font-weight: 700;
}

.addItemFormButton { 
    cursor: pointer;
    margin-top: 2em;
    border: 0;
    background-color: brown;
    color: white;
    font-size: 1em;
    font-weight: 600;
    width: 100%;
    padding: 0.8em 0;
    border-radius: 1em;
}
.addItemFormButton:hover { 
    background-color: rgb(122, 31, 31);
}



