.login { 
    max-width: 1000px;
    margin: 0 auto;
    padding: 0 1em;
}
.loginAuth { 
    background-color: var(--bg-block);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 3em 4em;
    border-radius: 1em;
    margin-top: 2em;
}

.loginBlockInfo { 
    max-width: 350px;
    display: flex;
    flex-direction: column;
}
.loginBlockInfoTitle { 
    color: var(--text-color-login);
    font-size: 1.8em;
    font-weight: 700;
    margin-bottom: 0.2em;
}
.loginBlockInfoDesc { 
    color: rgb(155, 168, 189);
    font-size: 0.9em;
    font-weight: 600;
    margin-bottom: 2em;
}

.loginBlockRight { 
}

.loginBlockRightImg { 
    width: 24em;
    border-radius: 1em;
}

.loginBlockInfoEmail { 
}

.loginBlockInfoEmailTitle, .loginBlockInfoPassTitle { 
    color: var(--text-color-login);
    margin-bottom: 0.3em;
    font-size: 0.95em;
    font-weight: 500;
}
.loginBlockInfoEmailInput, .loginBlockInfoPassInput { 
    border: 0;
    width: 100%;
    border-radius: 0.6em;
    padding: 0.6em 0.7em;
    color: rgba(0, 0, 0, 0.926);
    font-weight: 600;
    font-size: 1em;
    margin-bottom: 1em;
}

.loginBlockInfoEnter { 
    cursor: pointer;
    border: 0;
    font-size: 1em;
    font-weight: 500;
    letter-spacing: 0.04em;
    background-color: brown;
    padding: 0.6em 0;
    width: 100%;
    color: white;
    border-radius: 0.6em;
    transition: background-color .2s ease-in-out;
    margin-bottom: 2.5em;
}
.loginBlockInfoForgot { 
    cursor: pointer;
    font-size: 0.95em;
    color: var(--text-color-login);
    font-weight: 500;
    letter-spacing: 0.02em;
    text-align: right;
    margin-bottom: 1em;
    transition: color .2s ease-in-out;
}
.loginBlockInfoForgot:hover { 
    color: rgb(217, 56, 56);
}
.loginBlockInfoEnter:hover { 
    background-color: var(--hover-btn);
}
.loginBlockBottomReg { 
    display: flex;
    flex-direction: column;
    gap: 0.7em;
}
.loginBlockInfoReg{ 
    text-decoration: none;
    text-align: center;
    cursor: pointer;
    border: 0;
    font-size: 1em;
    font-weight: 600;
    background-color: var(--btn-register);
    padding: 0.6em 0;
    width: 100%;
    color: rgb(0, 0, 0);
    border-radius: 0.6em;  
    transition: background-color .2s ease-in-out;
}
.loginBlockInfoReg:hover { 
    background-color: var(--btn-register-hover);
}
.loginBlockInfoRegDesc { 
    color: var(--text-color-login);
    font-size: 0.93em;
    font-weight: 600;
}
.loginBlockInfoYs { 
    color: var(--text-color);
    font-size: 0.9em;
    font-weight: 500;
}


