.btn { 
    text-decoration: none;
    display: flex;
    align-items: center;
    gap: 0.5em;

    padding: 0.65em 1em;
    border-radius: 0.6em;
    cursor: pointer;
}
.btn:hover  { 
    background-color: var(--textBgHover);
}
.btn:hover .btnSvg { 
    stroke: rgb(224, 87, 87);
}
.btn:hover .btnText { 
    color: rgb(224, 87, 87);
}
.btnSvg { 
    stroke: rgb(194, 211, 237);
    stroke-width: 1;    
    width: 1.6em;
}

.btnText{ 
    text-decoration: none;
    color: rgb(194, 211, 237);

    font-size: 0.95em;
    font-weight: 500;
}

.noAuth {
    user-select: none;
    background-color: rgba(44, 44, 44, 0.579); /* Цвет фона с прозрачностью */
    position: absolute; /* Положение абсолютное, чтобы элемент занимал всю доступную область */
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    backdrop-filter: blur(2px); /* Эффект размытия фона */
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.noAuthInfo { 
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.9em;
}
.noAuthTitle { 
    color: rgb(213, 213, 213);
    font-weight: 700;
    font-size: 1.2em;
}
.noAuthSvg { 
    width: 3em;
    stroke: rgb(213, 213, 213);
    stroke-width: 1.3;
}

@media only screen and (max-width: 768px) {
    .btn { 
        text-decoration: none;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 0.2em;
    
        padding: 0.1em 1em;
        border-radius: 0.6em;
        cursor: pointer;
    }
    .btn:hover  { 
        background-color: var(--textBgHover);
    }
    .btn:hover .btnSvg { 
        fill: rgb(224, 87, 87);
    }
    .btn:hover .btnText { 
        color: rgb(224, 87, 87);
    }
    .btnSvg { 
        stroke: rgb(194, 211, 237);
        stroke-width: 1;    
        width: 1.6em;
    }
    
    .btnText{ 
        text-decoration: none;
        color: rgb(194, 211, 237);
    
        font-size: 0.7em;
        font-weight: 500;
    }
    
    .noAuth {
        user-select: none;
        background-color: rgba(44, 44, 44, 0.579); /* Цвет фона с прозрачностью */
        position: absolute; /* Положение абсолютное, чтобы элемент занимал всю доступную область */
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        backdrop-filter: blur(3px); /* Эффект размытия фона */
        display: flex;
        justify-content: space-around;
        align-items: center;
        border-radius: 2em 2em 0 0;
    }
    .noAuthInfo { 
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 0.2em;
    }
    .noAuthTitle { 
        color: rgb(213, 213, 213);
        font-weight: 600;
        font-size: 1em;
    }
    .noAuthSvg { 
        width: 2em;
        stroke: rgb(213, 213, 213);
        stroke-width: 1.1;
    }

}