@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap');
@import url(./reset.css);

* { 
  font-family: "Nunito Sans", sans-serif;
  color: var(--textColor);
}

:root { 
  --textBgHover: #373747;
  --textColorHover: rgb(208, 210, 222);
  --textColor: rgb(194, 211, 237);

  --text-color-login: rgb(244, 244, 244);
  --bg-block: rgb(29, 32, 42);
  --hover-btn: rgb(118, 30, 30);
  --text-color: rgb(194, 211, 237);

  --btn-register: white;
  --btn-register-hover: rgb(178, 178, 178);
}

input:focus{
  outline: none
}

.app { 
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: flex;
  overflow-y: auto;
  background-color: rgb(11, 12, 15);
}
.content { 
  flex: 1;
}

@media only screen and (max-width: 768px) {
  .app { 
    display: flex;
    background-color: rgb(11, 12, 15);
  }
  .content { 
    flex: 1;
  }
}