.cart { 
    background-color: rgb(29, 32, 42);
    padding: 0.9em;
    border-radius: 1em;
    border: 1px solid rgb(29, 32, 42);
    transition: background-color 0.2s ease-in-out, transform 0.2s ease-in-out;
}
.cartInfo { 
    display: flex;
    align-items: center;
    gap: 2em;
}
.cart:hover { 
    background-color: rgb(39, 43, 58);
    transform: translateY(-4px);
    border: 1px solid rgba(86, 94, 123, 0.829);
}
.cartImg { 
    width: 18em;
    height: 10em;
    border-radius: 1em;
    object-fit:cover;
}

.cartDesc { 
    display: flex;
    flex-direction: column;
    gap: 1em;
    width: 100%; /* Добавьте это свойство */
}
.cartDescTitle { 
    font-size: 1.2em;
    font-weight: 400;
    color: white;
}

.progressBlock { 
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 0.5em;
    background-color: rgba(21, 23, 30, 0.782);
    padding: 1em;
    border-radius: 1em;
}
.progressBar {
    background-color: brown;
    height: 0.3em;
    border-radius: 0.2em;
}
.progressBarFill {
    border: 1px solid brown;
    height: 0.4em;
    border-radius: 0.2em;
}
.progressText { 
    font-size: 1em;
    font-weight: 500;
}

.continueButton { 
    cursor: pointer;
    border: 0;
    background-color: brown;
    padding: 0.6em;
    width: 30%;
    color: white;
    border-radius: 0.7em;
    transition: background-color 0.2s ease-in-out;
}
.continueButton:hover { 
    background-color: rgb(122, 31, 31);
}