.home {
    position:relative;
    padding: 4.5em 2em;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    overflow-y: auto; /* Добавляем вертикальную прокрутку */
}
.carts { 
    display: flex;
    flex-wrap: wrap;
    gap: 2em;
    margin-top: 1em;
}

.categorieBtn { 
    position: absolute;
    cursor: pointer;
    top: 1em;
    left: 2em;
    cursor: pointer;
    border: 1px solid var(--text-color);
    background-color: var(--bg-block);
    padding: 0.5em 1em;
    border-radius: 0.5em;
    margin: 0 auto;
}
.categorieBtn label{ 
    cursor: pointer;
    
}
.categories{ 
    display: flex;
    flex-wrap: wrap;
    gap: 1em;
    border-radius: 1em;
}
.categorie { 
    user-select: none;
    display: flex;
    align-items: center;
    gap: 0.7em;
    cursor: pointer;
    background-color: rgb(39, 43, 57);
    padding: 0.5em 1em;
    border-radius: 0.8em;
}
.categorie:hover { 

    background-color: rgb(29, 32, 42);

}
.categorie label { 
    cursor: pointer;
    color:  rgb(255, 255, 255);
    font-size: 1em;
    font-weight: 500;
}

.categorieIcon { 
    width: 2em;
}
.selected {
    cursor: pointer;
    border: 1px solid var(--text-color);
}

@media only screen and (max-width: 768px) {
    .categories{ 
        display: flex;
        flex-wrap: wrap;
        margin-top: 3em;
        gap: 1em;
        border-radius: 1em;
    }
    .categorieBtn { 
        position: absolute;
        cursor: pointer;
        top: 1em;
        left: 0.7em;
        cursor: pointer;
        border: 1px solid var(--text-color);
        background-color: var(--bg-block);
        padding: 0.4em 0.9em;
        border-radius: 0.5em;
        margin: 0 auto;
    }
    .categorieBtn label{ 
        cursor: pointer;
        font-size: 0.8em;
        font-weight: 600;
    }
    .home {
        padding: 1em 0.7em 8em 0.7em;
        display: flex;
        overflow-y: auto; /* Добавляем вертикальную прокрутку */
    }
    .carts { 
        display: flex;
        justify-content: space-between;   
        flex-wrap: wrap;
        gap: 0;
        margin-top: 3em;
     }
    .categories{ 
        display: flex;
        flex-wrap: wrap;
        
        gap: 0.5em;
        
    }
    .categorie { 
        user-select: none;
        display: flex;
        align-items: center;
        gap: 0.7em;
        cursor: pointer;
        background-color: rgb(39, 43, 57);
        padding: 0.3em 0.8em;
        border-radius: 0.6em;
    }
    .categorie:hover { 
    
        background-color: rgb(29, 32, 42);
    
    }
    .categorie label { 
        cursor: pointer;
        color:  rgb(255, 255, 255);
        font-size: 0.8em;
        font-weight: 500;
    }
    
    .categorieIcon { 
        width: 1.7em;
    }
    .selected {
      border: 0.1em solid white;
    }

}
