.moder { 
    background-color: rgb(11, 12, 15);
    height: 91vh;
    padding: 2em;
    overflow-y: auto; /* Добавляем вертикальную прокрутку */
}
.moderTitle { 
    font-size: 2em;
    font-weight: 700;
    color: white;
    margin-bottom: 1em;
}
.moderCarts { 
    display: flex;
    align-items: center;
    gap: 1em;
    flex-wrap: wrap;
}