@media only screen and (max-width: 1920px) {
    .navbar { 
        position: relative;
        background-color: rgb(29, 32, 42);
        width: 15em;
        height: 100vh;
        padding: 1em 0.7em;
        flex: 0 0 auto;
        border-right: 0.1em solid rgb(43, 45, 60);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
    }
    .navbarLine { 
        background-color: rgb(43, 45, 60);
        margin: 0.5em 0;
        height: 0.01em;
    }
    .navbarLogo { 
        user-select: none;
        border-radius: 0.5em;
        position: relative;
    }
    .navbarLogo:hover { 
        background-color: rgba(255, 255, 255, 0.026);
    }
    .navbarTitle { 
        display: block;
        text-align: center;
        text-decoration: none;
        cursor: pointer;
        font-size: 2em;
        padding: 0.5em 0.5em 1em 0.5em;
        font-weight: 800;
        color: white;
    }
    .navbarTitleS { 
        text-align: center;
        cursor: pointer;
        font-weight: 800;
        color: rgb(193, 51, 51); 
    }
    .navbarTitleB { 
        text-decoration: none;
        position: absolute;
        top: 3.8em;
        right: 0;
        left: 0;
        text-align: center;
        font-size: 0.95em;
        font-weight: 600;
        color: rgb(91, 91, 91);
        cursor: pointer;
    }
    .navbarMenu { 
        margin-top: 2em;
        display: flex;
        flex-direction: column;
        gap: 0.4em;
    }
    .menuLink { 
        text-decoration: none;
        display: flex;
        align-items: center;
        gap: 0.5em;
        padding: 0.65em 1em;
        border-radius: 0.6em;
        transition: background-color 0.17s ease-in-out, color 0.17s ease-in-out;
    }
    .menuLink:hover { 
        background-color: var(--textBgHover);
    }
    .menuLink:hover .menuLinkText { 
        color: rgb(224, 87, 87);
    }
    .menuLink:hover .menuLinkSvg { 
        stroke: rgb(224, 87, 87);
    }
    .menuLink:hover .menuLinkSvgA { 
        fill: rgb(224, 87, 87);
    }
    .menuLinkSvg { 
        transition: stroke 0.17s ease-in-out;
        width: 1.5em;
        stroke: rgb(194, 211, 237);
        stroke-width: 1.4;
    }
    .menuLinkSvgA { 
        transition: fill 0.17s ease-in-out;
        width: 1.5em;
        fill: rgb(194, 211, 237);
        stroke-width: 1.4;
    }
    .menuLinkText { 
        transition: color 0.17s ease-in-out;
        text-decoration: none;
        color: rgb(194, 211, 237);
        font-size: 0.95em;
        font-weight: 500;
    }
    .navbarAddLink { 
        margin-bottom: 2em;
        text-decoration: none;
        color: white;
        background-color: brown;
        padding: 0.56em 2em;
        border-radius: 0.5em;
        font-weight: 600;
        text-align: center;
        margin-top: 1em;
        transition: background-color 0.17s ease-in-out;
    }
    .navbarAddLink:hover { 
        background-color: rgb(117, 29, 29);
    }
}

@media only screen and (max-width: 768px) {
    .navbar {
        background-color: rgb(29, 32, 42);
        padding: 0;
        flex: none;
        border-right: 0em solid rgb(43, 45, 60);
        z-index: 2;
        position: fixed;
        display: flex;
        justify-content: space-between;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 10vh;
        border-radius: 2em 2em 0 0;
    }
    .navbarLine { 
        background-color: rgb(43, 45, 60);
        margin: 0.5em 0;
        height: 0.01em;
    }
    .navbarLogo { 
        display: none;
    }
    .navbarLogo:hover { 
        background-color: rgba(255, 255, 255, 0.026);
    }
    .navbarTitle { 
       
    }
    .navbarTitleS { 
         
    }
    .navbarTitleB { 
        
    }
    .navbarMenu { 
        margin-top: 0;
        display: flex;
        flex-direction: row;
        gap: 0.5em;
    }
    .menuLink { 
        text-decoration: none;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 0.3em;
        padding: 0.1em 1em;
        border-radius: 0.6em;
        transition: background-color 0.17s ease-in-out, color 0.17s ease-in-out;
    }
    .menuLink:hover { 
        background-color: var(--textBgHover);
    }
    .menuLink:hover .menuLinkText { 
        color: rgb(224, 87, 87);
    }
    .menuLink:hover .menuLinkSvg { 
        stroke: rgb(224, 87, 87);
    }
    .menuLink:hover .menuLinkSvgA { 
        fill: rgb(224, 87, 87);
    }
    .menuLinkSvg { 
        transition: stroke 0.17s ease-in-out;
        width:1.6em;
        stroke: rgb(194, 211, 237);
        stroke-width: 1;
    }
    .menuLinkSvgA { 
        transition: fill 0.17s ease-in-out;
        width: 1.6em;
        fill: rgb(194, 211, 237);
        stroke-width: 0;
    }
    .menuLinkText { 
        transition: color 0.17s ease-in-out;
        text-decoration: none;
        color: rgb(194, 211, 237);
        font-size: 0.7em;
        font-weight: 500;
    }
    .navbarAddLink { 
        display: none;
        margin-bottom: 0;
        text-decoration: none;
        color: white;
        background-color: brown;
        padding: 0.56em 2em;
        border-radius: 0.5em;
        font-weight: 600;
        text-align: center;
        margin-top: 0;
        transition: background-color 0.17s ease-in-out;
    }
    .navbarAddLink:hover { 
        background-color: rgb(117, 29, 29);
    }    
}
