.header {
    
}
.container { 
    background-color: rgb(29, 32, 42);
    border-bottom: 0.1em solid rgb(43, 45, 60);
    height: 9vh;
    padding: 0 4em;
    display: flex;
    align-items: center;
    justify-content: left;
}
.navbarAddLink { 
    display: none;
}
.headerCatalog { 
    text-decoration: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 0.4em;
    padding: 0.3em 0.5em;
    border-radius: 0.6em;
}
.headerCatalog:hover .headerCatalogText { 
    color: rgb(224, 87, 87);
}
.headerCatalog:hover .headerCatalogSvg { 
    stroke: rgb(224, 87, 87);
}
.headerCatalogText { 
    text-decoration: none;
    font-size: 1.1em;
    font-weight: 500;
    color: rgb(194, 211, 237);
}
.headerCatalogSvg { 
    width: 2em;
    stroke: rgb(194, 211, 237);
    stroke-width: 1.2;
}

.headerAuth { 
    text-decoration: none;
    color: white;
    padding: 0.5em 1.6em;
    border-radius: 0.6em;
    background-color: brown;
    font-size: 1.1em;
    font-weight: 600;
}
.headerAuth:hover { 
    background-color: rgb(120, 31, 31);
}

.auth { 
    display: flex;
    align-items: center;
}

@media only screen and (max-width: 768px) {
    .navbarAddLink { 
        display: block;
        position: absolute;
        right: 2em;
        text-decoration: none;
        color: white;
        background-color: brown;
        padding: 0.6em 1em;
        border-radius: 0.5em;
        font-size: 0.8em;
        font-weight: 600;
        text-align: center;
        transition: background-color 0.17s ease-in-out;
    }
    .navbarAddLink:hover { 
        background-color: rgb(117, 29, 29);
    }
    .container { 
        background-color: rgb(29, 32, 42);
        border-bottom: 0.1em solid rgb(43, 45, 60);
        height: 9vh;
        padding: 0 0.5em;
        display: flex;
        align-items: center;
        justify-content: space-around;
    }
    .navbarLogo { 
        display: block;
        user-select: none;
        border-radius: 0.5em;
        position: absolute;
        left: 1em;
    }
    .navbarLogo:hover { 
        background-color: rgba(255, 255, 255, 0.026);
    }
    .navbarTitle { 
        display: block;
        text-align: center;
        text-decoration: none;
        cursor: pointer;
        font-size: 1.2em;
        padding: 0.5em 0.5em 1em 0.5em;
        font-weight: 800;
        color: white;
    }
    .navbarTitleS { 
        text-align: center;
        cursor: pointer;
        font-weight: 800;
        color: rgb(193, 51, 51); 
    }
    .navbarTitleB { 
        text-decoration: none;
        position: absolute;
        top: 2.9em;
        right: 0;
        left: 0;
        text-align: center;
        font-size: 0.75em;
        font-weight: 600;
        color: rgb(91, 91, 91);
        cursor: pointer;
    }


    .headerCatalog { 
        text-decoration: none;
        cursor: pointer;
        display: flex;
        align-items: center;
        gap: 0.4em;
        padding: 0.3em 0.5em;
        border-radius: 0.6em;
    }
    .headerCatalog:hover .headerCatalogText { 
        color: rgb(224, 87, 87);
    }
    .headerCatalog:hover .headerCatalogSvg { 
        stroke: rgb(224, 87, 87);
    }
    .headerCatalogText { 
        display: none;
        text-decoration: none;
        font-size: 0.8em;
        font-weight: 500;
        color: rgb(194, 211, 237);
    }
    .headerCatalogSvg { 
        display: none;
        width: 1.5em;
        stroke: rgb(194, 211, 237);
        stroke-width: 1.2;
    }

    .headerAuth { 
        text-decoration: none;
        color: white;
        padding: 0.5em 1.6em;
        border-radius: 0.6em;
        background-color: brown;
        font-size: 0.8em;
        font-weight: 600;
    }
    .headerAuth:hover { 
        background-color: rgb(120, 31, 31);
    }
    
    .auth { 
        position: absolute;
        right: 1em;
        display: flex;
        align-items: center;
    }
}
  
  @media only screen and (min-width: 769px) {
    .navbarLogo {
      display: none; /* Скрываем логотип */
    }
  }